<template>
  <div>
    <el-form
      class="detail-form-content"
      ref="ruleForm"
      :model="ruleForm"
      label-width="80px"
    >  
     <el-row>
                    <el-col :span="12">
           <el-form-item v-if="flag=='laoshi'"  label='' prop="laoshiUuidNumber">
               <el-input v-model="ruleForm.laoshiUuidNumber"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='laoshi'"  label='' prop="laoshiName">
               <el-input v-model="ruleForm.laoshiName"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='laoshi'"  label='' prop="laoshiPhone">
               <el-input v-model="ruleForm.laoshiPhone"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='laoshi'"  label='' prop="laoshiIdNumber">
               <el-input v-model="ruleForm.laoshiIdNumber"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
             <el-form-item v-if="flag=='laoshi'" label='' prop="laoshiPhoto">
                 <file-upload
                         tip=""
                         action="file/upload"
                         :limit="3"
                         :multiple="true"
                         :fileUrls="ruleForm.laoshiPhoto?ruleForm.laoshiPhoto:''"
                         @change="laoshiPhotoUploadChange"
                 ></file-upload>
             </el-form-item>
         </el-col>
         <el-col :span="12">
           <el-form-item v-if="flag=='laoshi'"  label='' prop="laoshiEmail">
               <el-input v-model="ruleForm.laoshiEmail"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='xizhuren'"  label='' prop="xizhurenUuidNumber">
               <el-input v-model="ruleForm.xizhurenUuidNumber"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='xizhuren'"  label='' prop="xizhurenName">
               <el-input v-model="ruleForm.xizhurenName"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='xizhuren'"  label='' prop="xizhurenPhone">
               <el-input v-model="ruleForm.xizhurenPhone"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='xizhuren'"  label='' prop="xizhurenIdNumber">
               <el-input v-model="ruleForm.xizhurenIdNumber"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
             <el-form-item v-if="flag=='xizhuren'" label='' prop="xizhurenPhoto">
                 <file-upload
                         tip=""
                         action="file/upload"
                         :limit="3"
                         :multiple="true"
                         :fileUrls="ruleForm.xizhurenPhoto?ruleForm.xizhurenPhoto:''"
                         @change="xizhurenPhotoUploadChange"
                 ></file-upload>
             </el-form-item>
         </el-col>
         <el-col :span="12">
           <el-form-item v-if="flag=='xizhuren'"  label='' prop="xizhurenEmail">
               <el-input v-model="ruleForm.xizhurenEmail"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

        <el-col :span="12" v-if="flag=='yonghu'">
            <el-form-item class="select" label="" prop="clazzId">
                <el-select v-model="ruleForm.clazzId" filterable placeholder="" @change="clazzChange">
                    <el-option
                            v-for="(item,index) in clazzOptions"
                            v-bind:key="item.id"
                                            :label="item.clazzName"
                            :value="item.id">
                    </el-option>
                </el-select>
            </el-form-item>
        </el-col>
         <el-col :span="12">
             <el-form-item v-if="flag=='yonghu'" label='' prop="laoshiId">
                 <el-input v-model="clazzForm.laoshiId" disabled></el-input>
             </el-form-item>
         </el-col>
         <el-col :span="12">
             <el-form-item v-if="flag=='yonghu'" label='' prop="clazzName">
                 <el-input v-model="clazzForm.clazzName" disabled></el-input>
             </el-form-item>
         </el-col>
         <el-col :span="12">
             <el-form-item v-if="flag=='yonghu'" label='' prop="clazzAddress">
                 <el-input v-model="clazzForm.clazzAddress" disabled></el-input>
             </el-form-item>
         </el-col>
         <el-col :span="12">
           <el-form-item v-if="flag=='yonghu'"  label='' prop="yonghuUuidNumber">
               <el-input v-model="ruleForm.yonghuUuidNumber"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='yonghu'"  label='' prop="yonghuName">
               <el-input v-model="ruleForm.yonghuName"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='yonghu'"  label='' prop="yonghuPhone">
               <el-input v-model="ruleForm.yonghuPhone"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
           <el-form-item v-if="flag=='yonghu'"  label='' prop="yonghuIdNumber">
               <el-input v-model="ruleForm.yonghuIdNumber"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-col :span="12">
             <el-form-item v-if="flag=='yonghu'" label='' prop="yonghuPhoto">
                 <file-upload
                         tip=""
                         action="file/upload"
                         :limit="3"
                         :multiple="true"
                         :fileUrls="ruleForm.yonghuPhoto?ruleForm.yonghuPhoto:''"
                         @change="yonghuPhotoUploadChange"
                 ></file-upload>
             </el-form-item>
         </el-col>
         <el-col :span="12">
           <el-form-item v-if="flag=='yonghu'"  label='' prop="yonghuEmail">
               <el-input v-model="ruleForm.yonghuEmail"  placeholder='' clearable></el-input>
           </el-form-item>
         </el-col>

         <el-form-item v-if="flag=='users'" label="" prop="username">
             <el-input v-model="ruleForm.username"
                       placeholder=""></el-input>
         </el-form-item>
         <el-col :span="12">
             <el-form-item v-if="flag!='users'"  label="" prop="sexTypes">
                 <el-select v-model="ruleForm.sexTypes" placeholder="">
                     <el-option
                             v-for="(item,index) in sexTypesOptions"
                             v-bind:key="item.codeIndex"
                             :label="item.indexName"
                             :value="item.codeIndex">
                     </el-option>
                 </el-select>
             </el-form-item>
         </el-col>
         <el-col :span="24">
             <el-form-item>
                 <el-button type="primary" @click="onUpdateHandler"> </el-button>
             </el-form-item>
         </el-col>
     </el-row>
    </el-form>
  </div>
</template>
<script>
// url
import { isNumber,isIntNumer,isEmail,isMobile,isPhone,isURL,checkIdCard } from "@/utils/validate";

export default {
  data() {
    return {
        ruleForm: {},
        flag: '',
        usersFlag: false,
        // sexTypesOptions : [],
//  
    //  
        // 
        sexTypesOptions : [],
//  
    //  
        // 
        sexTypesOptions : [],
//  
    //  
        // 
        sexTypesOptions : [],
    //  
        // 
        clazzOptions : [],
        clazzForm:{},
    };
  },
  mounted() {
    //
    var table = this.$storage.get("sessionTable");
    this.sessionTable = this.$storage.get("sessionTable");
    this.role = this.$storage.get("role");
    if (this.role != ""){
    }

    this.flag = table;
    this.$http({
      url: `${this.$storage.get("sessionTable")}/session`,
      method: "get"
    }).then(({ data }) => {
      if (data && data.code === 0) {
        this.ruleForm = data.data;
//  
//  
//  
    //  
        if(table == 'yonghu'){
            this.clazzChange(this.ruleForm.clazzId);
        }
      } else {
        this.$message.error(data.msg);
      }
    });

//   
//   
//   
    //  
        // 
    this.$http({
        url: `clazz/page?page=1&limit=100`,
        method: "get"
    }).then(({ data }) => {
        if (data && data.code === 0) {
        this.clazzOptions = data.data.list;
        }else{
            this.$message.error(data.msg);
        }
    });

      this.$http({
          url: `dictionary/page?page=1&limit=100&sort=&order=&dicCode=sex_types`,
          method: "get"
      }).then(({ data }) => {
          if (data && data.code === 0) {
              this.sexTypesOptions = data.data.list;
          } else {
              this.$message.error(data.msg);
          }
      });
  },
  methods: {
    chongzhi() {
      this.$router.replace({ path: "/pay" });
    },
    laoshiPhotoUploadChange(fileUrls) {
        this.ruleForm.laoshiPhoto = fileUrls;
    },
    xizhurenPhotoUploadChange(fileUrls) {
        this.ruleForm.xizhurenPhoto = fileUrls;
    },
    yonghuPhotoUploadChange(fileUrls) {
        this.ruleForm.yonghuPhoto = fileUrls;
    },
// 
    clazzChange(id){
        this.$http({
            url: `clazz/info/`+id,
            method: "get"
        }).then(({ data }) => {
            if (data && data.code === 0) {
                this.clazzForm = data.data;
            }
        });
    },


    onUpdateHandler() {
                         if((!this.ruleForm.laoshiUuidNumber)&& 'laoshi'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.laoshiName)&& 'laoshi'==this.flag){
                             this.$message.error('');
                             return
                         }

                             if( 'laoshi' ==this.flag && this.ruleForm.laoshiPhone&&(!isMobile(this.ruleForm.laoshiPhone))){
                                 this.$message.error(``);
                                 return
                             }
                         if((!this.ruleForm.laoshiIdNumber)&& 'laoshi'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.laoshiPhoto)&& 'laoshi'==this.flag){
                             this.$message.error('');
                             return
                         }

                             if( 'laoshi' ==this.flag && this.ruleForm.laoshiEmail&&(!isEmail(this.ruleForm.laoshiEmail))){
                                 this.$message.error(``);
                                 return
                             }
                         if((!this.ruleForm.xizhurenUuidNumber)&& 'xizhuren'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.xizhurenName)&& 'xizhuren'==this.flag){
                             this.$message.error('');
                             return
                         }

                             if( 'xizhuren' ==this.flag && this.ruleForm.xizhurenPhone&&(!isMobile(this.ruleForm.xizhurenPhone))){
                                 this.$message.error(``);
                                 return
                             }
                         if((!this.ruleForm.xizhurenIdNumber)&& 'xizhuren'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.xizhurenPhoto)&& 'xizhuren'==this.flag){
                             this.$message.error('');
                             return
                         }

                             if( 'xizhuren' ==this.flag && this.ruleForm.xizhurenEmail&&(!isEmail(this.ruleForm.xizhurenEmail))){
                                 this.$message.error(``);
                                 return
                             }
                         if((!this.ruleForm.clazzId)&& 'yonghu'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.yonghuUuidNumber)&& 'yonghu'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.yonghuName)&& 'yonghu'==this.flag){
                             this.$message.error('');
                             return
                         }

                             if( 'yonghu' ==this.flag && this.ruleForm.yonghuPhone&&(!isMobile(this.ruleForm.yonghuPhone))){
                                 this.$message.error(``);
                                 return
                             }
                         if((!this.ruleForm.yonghuIdNumber)&& 'yonghu'==this.flag){
                             this.$message.error('');
                             return
                         }

                         if((!this.ruleForm.yonghuPhoto)&& 'yonghu'==this.flag){
                             this.$message.error('');
                             return
                         }

                             if( 'yonghu' ==this.flag && this.ruleForm.yonghuEmail&&(!isEmail(this.ruleForm.yonghuEmail))){
                                 this.$message.error(``);
                                 return
                             }
        if((!this.ruleForm.sexTypes) && this.flag!='users'){
            this.$message.error('');
            return
        }
      if('users'==this.flag && this.ruleForm.username.trim().length<1) {
        this.$message.error(``);
        return	
      }
      this.$http({
        url: `${this.$storage.get("sessionTable")}/update`,
        method: "post",
        data: this.ruleForm
      }).then(({ data }) => {
        if (data && data.code === 0) {
          this.$message({
            message: "",
            type: "success",
            duration: 1500,
            onClose: () => {
            }
          });
        } else {
          this.$message.error(data.msg);
        }
      });
    }
  }
};
</script>
<style lang="scss" scoped>
</style>
